import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home'
import Menus from '@/components/Menus'
import Titlebar from "@/components/titlebar";
import Coin from "@/components/coin";
import titlebar from "@/components/titlebar";
import Estate from "@/components/estate"
import Stock from "@/components/stock"

Vue.use(VueRouter)

const routes = [
  {
    //메인 홈
    path: '/',
    name: 'Home',
    components: {
      title:Titlebar,
      menu: Menus,
      content: Home,
    },
    props: true,
  },
  {
    //로그인
    path: '/Login',
    name: 'Login',
    component: () => import('@/views/Login')
  },
  {
    //회원가입
    path: '/signup',
    name: 'Signup',
    component: () => import('@/components/signup')
  },
  {
    //회원가입 완료창
    path: '/signup_complete',
    name: 'SignupComplete',
    component: () => import('@/components/signup_complete')
  },
  //여기는 메뉴
  {
    path:'/coin',
    name:'Coin',
    components: {
      title:titlebar,
      menu: Menus,
      content: Coin,
    },
  },
  {
    path:'/estate',
    name:'Estate',
    components: {
      title: titlebar,
      menu:Menus,
      content: Estate,
    }
  },
  {
    path:'/stock',
    name:'Stock',
    components: {
      title: titlebar,
      menu:Menus,
      content: Stock,
    }
  }


]

const router = new VueRouter({
  routes,
  mode: 'history',
  base: '/',
})

export default router
