<template>
  <div>
    <v-navigation-drawer
        id="app-drawer"
        v-model="drawer"
        app
        dark
        floating
        persistent
        width="280"
        class="grey darken-4"

    >
      <div>
        <v-layout
            class="fill-height"
            tag="v-list"
            column
        >
          <v-list>
            <v-list-item @click="movePage('');">
              <v-toolbar-title><v-icon class="mr-2">menu</v-icon>Title</v-toolbar-title>
            </v-list-item>
            <hr class="mt-2 mb-2">
            <v-list-item-group active-class="white--text">
              <template v-for="menu in menus">
                <!-- 자식메뉴가 있는 경우.-->
                <template v-if="menu.childrens">
                  <v-list-group
                    :prepend-icon="menu.icon"
                    :key="menu.id">
                    <template v-slot:activator>
                      <v-list-item-title>{{menu.title}}</v-list-item-title>
                    </template>
                    <template v-for="children in menu.childrens">
                      <v-list-item
                        @click="movePage(children.target);"
                        :key="children.id"
                        class="ml-2">
                        <v-icon>{{children.icon}}</v-icon>
                        <v-list-item-title>
                          {{children.title}}
                        </v-list-item-title>
                      </v-list-item>
                    </template>
                  </v-list-group>
                </template>
                <template v-else>
                  <v-list-item
                    @click="movePage(menu.target);"
                    :key="menu.id"
                    >
                    <v-list-item-icon>
                      <v-icon>{{menu.icon}}</v-icon>
                    </v-list-item-icon>
                    <v-list-item-title>{{menu.title}}</v-list-item-title>
                  </v-list-item>
                </template>

              </template>
            </v-list-item-group>
          </v-list>
        </v-layout>
      </div>
    </v-navigation-drawer>
  </div>
</template>

<script>
import { mapState } from "vuex";
import _ from "lodash";

export default {
  components: {
  },
  data: () => ({
    drawer: null,
    color: 'success',
    responsive: false,
  }),
  computed: _.extend(
      mapState(["menus"]),
  ),
  mounted(){
    this.onResponsiveInverted()
    window.addEventListener('resize', this.onResponsiveInverted)
  },
  methods: {
    movePage(target){
      this.$router.push({ name: target });
    },
    onResponsiveInverted () {
      if (window.innerWidth < 1000) {
        this.responsive = true
      } else {
        this.responsive = false
      }
    },
  },
}
</script>

<style lang="scss">
#app-drawer {
  .v-list__tile {
    border-radius: 4px;
    &--buy {
      margin-top: auto;
      margin-bottom: 17px;
    }
  }
}
</style>